import Link from 'next/link'
import Navbar from 'react-bootstrap/Navbar'
import Nav from 'react-bootstrap/Nav'
import ListGroup from 'react-bootstrap/ListGroup'


const mainStyle = {
  background: 'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)',
  position: 'sticky',
  top:'0',
  width: '100%',
  transition:'all 0.5s',
  zIndex: '1'
}

const Header = () => {
  return (
    <Navbar collapseOnSelect expand="lg" style={mainStyle}>
      <Navbar.Brand href="#home">NextJS with Bootstrap</Navbar.Brand>
      <Navbar.Toggle aria-controls="responsive-navbar-nav" />
      <Navbar.Collapse id="responsive-navbar-nav">
        <Nav className="mr-auto">
          {
            getPosts().map(header => (
              <ListGroup variant="flush" key={header.id}>
                <ListGroup.Item>
                  <PostLink key={header.id} post={header} />
                </ListGroup.Item>
              </ListGroup>
            ))
          }
        </Nav>
      </Navbar.Collapse>
    </Navbar>
  )
}

// 使用function作爲api承接內容，在上方的return中map接到的值
const getPosts = () => {
  return [
    { id: '/', title: 'Home' },
    { id: '/about', title: 'About' },
    { id: '/batman', title: 'Batman' },
    { id: '/contact', title: 'Contact' },
    { id: '/login', title: 'Login' }
  ]
}

// 當作額外插入的link來使用，這裡面的header.id，header.title都是藉由被map進來的header值使用
const PostLink = ({ header }) => (
    <Link href={`/${header.id}`} >
      <a>{header.title}</a>
    </Link>
)

export default Header

